<template>
<div>
    <nav class="navbar navbar-light navbar-expand-lg fixed-top bg-white clean-navbar">
        <div class="container"><a class="navbar-brand logo" href="#">Viral Videos</a><button data-toggle="collapse" class="navbar-toggler" data-target="#navcol-1"><span class="sr-only">Toggle navigation</span><span class="navbar-toggler-icon"></span></button>
            <div class="collapse navbar-collapse"
                id="navcol-1">
                <ul class="nav navbar-nav ml-auto">
                    <li class="nav-item" role="presentation"><a class="nav-link" href="#categories">Categories</a></li>
                </ul>
            </div>
        </div>
    </nav>
    <main class="page catalog-page">
        <section class="clean-block clean-catalog dark">
            <div class="container">
                <div class="block-heading">
                        <div class="container">
                            <div class="carousel slide" data-ride="carousel" id="carousel-1">
                                <div class="carousel-inner" role="listbox" style="width: 100%;height: 400px;">
                                    <div class="carousel-item active">
                                        <img class="img-responsive" src="../../static/assets/img/1.jpg" alt="Slide Image">
                                    </div>
                                    <div v-for="image in images" :key="image.id" class="carousel-item">
                                        <img class="img-responsive" :src="picture(image)" :alt="picture(image)">
                                    </div>
                                </div>
                                <div><a class="carousel-control-prev" href="#carousel-1" role="button" data-slide="prev"><span class="carousel-control-prev-icon"></span><span class="sr-only">Previous</span></a><a class="carousel-control-next" href="#carousel-1" role="button"
                                        data-slide="next"><span class="carousel-control-next-icon"></span><span class="sr-only">Next</span></a></div>
                                <ol class="carousel-indicators">
                                    <li data-target="#carousel-1" data-slide-to="0" class="active"></li>
                                    <li data-target="#carousel-1" data-slide-to="1"></li>
                                    <li data-target="#carousel-1" data-slide-to="2"></li>
                                    <li data-target="#carousel-1" data-slide-to="3"></li>
                                    <li data-target="#carousel-1" data-slide-to="4"></li>
                                </ol>
                            </div>
                        </div>
                </div>
                <div class="content" id="categories">
                    <div class="row">
                        <div class="col-md-3">
                            <div class="d-none d-md-block">
                                <div class="filters">
                                    <div class="filter-item">
                                        <h3>Choose category</h3>
                                        <div class="form-group">
                                            <div class="col-sm-10">
                                                <select name="Category" v-model="videosToDisplay.category" class="form-control">
                                                    <option value="">All</option>
                                                    <option v-for="catg in Object.keys(categories)" :key="catg.id">{{catg}}</option>
                                                </select>
                                            </div>
                                        </div>
                                        <div class="form-check" v-if="categories[videosToDisplay.category]">
                                            <div v-for="subcat in categories[videosToDisplay.category]" :key="subcat.id">
                                                <label>
                                                  <input type="checkbox">&nbsp;&nbsp;{{subcat}}
                                                </label><br>
                                            </div>
                                        </div>
                                        <div class="form-check" v-else>
                                        <small>⬆<em>Showing all videos. Select a category to view</em></small>
                                        </div>
                                     </div>
                                </div>
                            </div>
                            <div class="d-md-none"><a class="btn btn-link d-md-none filter-collapse" data-toggle="collapse" aria-expanded="false" aria-controls="filters" href="#filters" role="button">Filters<i class="icon-arrow-down filter-caret"></i></a>
                                <div class="collapse"
                                    id="filters">
                                    <div class="filters">
                                        <div class="filter-item">
                                            <h3>Categories</h3>
                                            <div class="form-group">
                                                <div class="col-sm-10">
                                                    <select name="Category" v-model="videosToDisplay.category" class="form-control">
                                                        <option value="">All</option>
                                                        <option v-for="catg in Object.keys(categories)" :key="catg.id">{{catg}}</option>
                                                    </select>
                                                </div>
                                            </div>
                                             <div class="form-check" v-if="categories[videosToDisplay.category]">
                                                 <div v-for="subcat in categories[videosToDisplay.category]" :key="subcat.id">
                                                    <label>
                                                    <input type="checkbox">&nbsp;&nbsp;{{subcat}}
                                                    </label><br>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div class="col-md-9">
                            <div class="products">
                                <div v-if="errored" class="row">
                                    <div class="col-sm-12 col-lg-12">
                                        <div class="text-center alert alert-danger" role="alert">
                                            <p style="font-size: 100px;" class="text-center">
                                                <i class="fa fa-exclamation-circle" aria-hidden="true"></i>
                                            </p>
                                            <strong>Error:</strong> {{videos}}
                                        </div>
                                    </div>
                                </div>
                                <div v-else class="row">
                                    <div v-if="loading" class="col-sm-12 col-lg-12">
                                        <div class="text-center" style="font-size:50px">
                                            Please wait...<br>
                                            <i class="fa fa-refresh fa-spin" aria-hidden="true"></i>
                                        </div>
                                    </div>
                                    <div v-else class="col-sm-6 col-lg-4" :key="video.id" v-for="(video, i) in videos">
                                        <div class="card clean-card text-center" v-if="i < videosToDisplay.count">
                                            <div v-html="printRawHTML(video)"></div>
                                            <div class="card-body info">
                                                <h6 class="card-title" :title="video.title">{{shortenName(video.title)}}</h6>
                                                <button type="button" @click="selectedVideo = video" class="btn btn-success" data-toggle="modal" data-target="#modelId">
                                                    Download <i class="fa fa-download" aria-hidden="true"></i>
                                                </button>
                                                <br><small>{{convertToMB(video.lq_size) || convertToMB(video.hq_size) || '-'}}</small>
                                                <br>
                                                <a title="share video with friends" href="#share">
                                                    Share <i class="fa fa-share" aria-hidden="true"></i>
                                                </a>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                <nav>
                                    <ul class="pagination" v-if="videos.length > videosToDisplay.count" v-show="typeof(videos) === 'object'">
                                        <li class="page-item active">
                                            <button type="button" @click="videosToDisplay.count += 5" class="btn btn-primary btn-lg">Show more</button>
                                        </li>
                                    </ul>
                                    <ul class="pagination" v-else v-show="typeof(videos) === 'object'">
                                        <li class="page-item active">
                                            <em>That's all we have. We're glad you came this far 😊...</em>
                                        </li>
                                    </ul>
                                </nav>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </section>
    </main>
    <!-- Modal -->
    <div class="modal fade" id="modelId" tabindex="-1" role="dialog" aria-labelledby="modelTitleId" aria-hidden="true">
        <div class="modal-dialog" role="document">
            <div class="modal-content">
                <div class="modal-header">
                    <h5 class="modal-title">{{selectedVideo.title}}</h5>
                        <button type="button"  class="close" data-dismiss="modal" aria-label="Close">
                            <span aria-hidden="true">&times;</span>
                        </button>
                </div>
                <div class="modal-body">
                    <div class="form-check" v-if="selectedVideo.links">
                    <div
                      v-show="selectedVideoRaw.length > 10"
                      v-html="selectedVideoRaw"></div>
                    </div>
                    <div v-else>
                        <div class="form-check" v-if="downloadLoading">
                            Fetching download links. Please wait....
                            <i class="fa fa-refresh fa-spin" aria-hidden="true"></i>
                            <!-- selectedVideoProp(selectedVideo.url) -->
                        </div>
                        <div class="row form-check" v-else>
                            <div class="alert alert-danger text-center" role="alert">
                                <i style="color:red; font-size: 20px" class="fa fa-exclamation-triangle" aria-hidden="true"></i><br>
                                <strong>Error: </strong> {{selectedVideo.error}}
                            </div>
                        </div>
                    </div>
                </div>
                <div class="modal-footer">
                    <button type="button" class="btn btn-secondary" data-dismiss="modal">Close</button>
                    <!-- <button type="button" v-show="selectedVideo.links" class="btn btn-success">Download now <i class="fa fa-download" aria-hidden="true"></i> </button> -->
                </div>
            </div>
        </div>
    </div>
    <footer class="page-footer dark">
        <div class="container">
            <div class="row">
                <div class="col-sm-3">
                    <h5>About me</h5>
                    <ul>
                        <!-- <li><a href="#">Terms of Service</a></li> -->
                        <!-- <li><a href="#">Terms of Use</a></li> -->
                        <li>
                            <h1><i class="fa fa-male" aria-hidden="true"></i></h1>
                            <a href="#">@tasiukwaplong</a></li>
                    </ul>
                </div>
            </div>
        </div>
        <div class="footer-copyright">
            <p>&copy; {{new Date().getFullYear()}}</p>
        </div>
    </footer>
</div>
</template>

<script>
import vc from '../controller/viralvideo.js'
export default {
  name: 'Home',
  data () {
    return {
      errored: false,
      loading: true,
      videos: [],
      selectedVideo: {},
      downloadLoading: false,
      selectedVideoRaw: '',
      videosToDisplay: {
        count: 10,
        category: ''
      },
      images: [],
      categories: {}
    }
  },
  methods: {
    printRawHTML: (data) => {
      return `<div
            class="fb-video"
            data-href="${data.url}"
            data-width="500"
            data-show-text="false">
                <div class="fb-xfbml-parse-ignore">
                <blockquote
                cite="${data.url}">
                  Video is Loading.....<br>
                  <i class="fa fa-refresh fa-spin" aria-hidden="true"></i><br>
                  <small><a href="#report">Video taking too long to load? </a></small>
                </blockquote>
                </div>
            </div>`
    //   return `<div class="fb-video"
    //       data-show-captions = "true"
    //       data-show-text = "true"
    //       data-href="${data.url}"
    //       data-allowfullscreen="true">
    //       Video Loading.....<br>
    //       <i class="fa fa-refresh fa-spin" aria-hidden="true"></i><br>
    //       <small><a href="#report">Video taking too long to load? </a></small>
    //     </div>`
    },
    shortenName: (title) => {
      if (title.trim().length >= 20) {
        return title.substring(0, 19) + ' ...'
      } else {
        let spaces = ''
        for (let i = 0; i < (31 - title.length); i++) {
          spaces += ' '
        }
        return title + spaces
      }
    },
    picture: (picUrl = '') => {
      return vc.pictureUrl(picUrl)
    },
    convertToMB: (sizeInBytes) => {
      if (!sizeInBytes) {
        return null
      }
      let sizeInMB = sizeInBytes / 1000000
      return sizeInMB.toFixed(2) + 'mb'
    }
  },
  watch: {
    selectedVideo: async function (video) {
      this.downloadLoading = true
      const downloadLinks = (video.url === undefined) ? null : await vc.getDownloadLinks(video.url)
      setTimeout(() => {
        // delay download button by 2 secs
        this.downloadLoading = (!downloadLinks)
      }, 2000)
      if (!downloadLinks.errored && typeof (downloadLinks.message) === 'object') {
        // console.log('found')
        this.selectedVideo['links'] = downloadLinks.message
        const data = downloadLinks.message
        const LQ_LINK = data['LQ_Link'] || {}
        const HQ_LINK = data['HQ_Link'] || {}
        // console.log(LQ_LINK.link)
        this.selectedVideoRaw = (data['LQ_Link'] || data['HQ_Link'])
          ? `<video width="100%" height="auto" controls>
            <source src="${LQ_LINK.link || HQ_LINK.link}" type="video/mp4">
            <source src="${LQ_LINK.link || HQ_LINK.link}" type="video/ogg">
                Your browser does not support the video tag.
            </video>
            <div class="col-md-6 text-center" style="color:white">
                <label class="form-check-label">
                &nbsp;&nbsp;
                <a class="btn-block btn btn-success" href="${LQ_LINK.link}" target="_blank">
                Low quality (${this.convertToMB(LQ_LINK.size) || '-'})
                 <i class="fa fa-download" aria-hidden="true"></i>
                </a>
                </label>
                <br>
                <label class="form-check-label">
                &nbsp;&nbsp;
                <a class="btn-block btn btn-success" href="${HQ_LINK.link}" target="_blank">
                High quality (${this.convertToMB(HQ_LINK.size) || '-'})
                 <i class="fa fa-download" aria-hidden="true"></i>
                </a>
                </label>
            </div>`
          : '<h1 class="text-center">An unknown error has occured</h1>'
      } else {
        this.selectedVideo.error = downloadLinks.message
      }
    }
  },
  mounted: async function () {
    // before component loads
    const data = await vc.getVideos()
    this.loading = (data === undefined)
    this.errored = data.errored
    this.videos = data.message
    if (typeof (this.videos) === 'object') {
      this.videos.forEach((d) => {
        if (d.image_src.length >= 5 && this.images.length < 5) this.images.push(d.image_src)
        if (d.sub_linked.length >= 1) {
          // if video category has sub category
          (this.categories[d.category] === undefined)
            ? this.categories[d.category] = [d.sub_linked]
            : this.categories[d.category].push(d.sub_linked)
        } else {
          if (this.categories[d.category] === undefined) this.categories[d.category] = []
        }
      })
    }
  }
}
</script>



